import React from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import
import Aux from "../../../hoc/_Aux";
import SetmoneyDriver from './Change.Prix'
var urlbackend = require("../../../env/env")();

class ListCaisseDriver extends React.Component {
  state = {
    Drivers: [],
  };

  componentDidMount() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };

    axios
      .get(urlbackend + "/driver", config)
      .then((response) => {
        console.log("this respon list", response.data);
        this.setState({ Drivers: response.data.data });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  Delette(Driver) {
    console.log("hellodelet", Driver.id);
    confirmAlert({
      title: "Confirm Delete",
      message: "Are you sure to do Delete?",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            axios
              .delete("http://localhost:5000/api/v1/Drivers/" + Driver.id)
              .then(() => {
                var array = [...this.state.Drivers];
                var index = array.indexOf(Driver);
                if (index !== -1) {
                  array.splice(index, 1);
                  this.setState({ Drivers: array });
                }
              });
          },
        },
        {
          label: "No",
          onClick: null,
        },
      ],
    });
    /*  */
  }
  render() {
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">
                 caisse 
                  <Link to="/Add/driver">
                    {" "}
                    <Button> Ajouter Driver</Button>{" "}
                  </Link>{" "}
                </Card.Title>
              </Card.Header>
              <Card.Body>
                <Table striped responsive>
                  <thead>
                    <tr>
                      <th>Nom</th>
                      <th>username</th>
                      <th>N. de téléphone</th>
                      <th>agence</th>
                      <th>Montant</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.Drivers.map((Driver, index) => (
                      <tr key={Driver.id}>
                        <td>
                          {Driver.firstname} {Driver.lastname}
                        </td>
                        <td>{Driver.username} </td>
                        <td>{Driver.phone_number} </td>
                        <td>{Driver.zone?.name} </td>
                        <td      style={{ "fontWeight": "bold" }} className="font-weight-bold text-black" > {Driver.frais_livraision}  TND </td>
                        <td>
                          {" "}
                        
                        <SetmoneyDriver  price={Driver.frais_livraision}  drive={Driver.id} change={()=> this.componentDidMount()}  />
                          <Link to={"/history/driver/" + Driver.id}>
                            <Button
                              variant="success"
                              onClick={() => console.log("not yet")}
                            >
                              {" "}
                              <span className="fa fa-file"></span>
                            </Button>
                          </Link>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default ListCaisseDriver;
